import React from 'react';
import {Tabs, Tab} from 'material-ui/Tabs';
import FontIcon from 'material-ui/FontIcon';
import ActionFlightTakeoff from 'material-ui/svg-icons/action/flight-takeoff';
import ConnectedLoginPage from '../Login'

class TabsNavigation extends React.Component{

    constructor(props){

        super(props);
        this.state = {
            value: 'feed',
        };
    }


    handleChange = (newValue) => {
        this.setState({
            value: newValue,
        });
    };

    render(){


        return (

        <Tabs>
            value={this.state.value}
            onChange={this.handleChange}

            <Tab icon={<FontIcon className="muidocs-icon-action-home" />} label = " Feed">
                <div>
                    some text
                </div>
            </Tab>
            <Tab icon={<ActionFlightTakeoff />}>
                <ConnectedLoginPage></ConnectedLoginPage>
            </Tab>
            <Tab icon={<FontIcon className="material-icons">favorite</FontIcon>} />
        </Tabs>

        )
    }





}



export default TabsNavigation;


